import React, { PureComponent } from 'react'
import klinecharts from 'klinecharts'
import FileSaver from 'file-saver'
import SettingList from './settinglist/SettingList'
import set from 'lodash/set'

import './kline.less'

class KLine extends PureComponent {
  constructor (props) {
    super(props)
    this.chart = null
  }

  componentDidMount () {
    this.chart = klinecharts.init(document.getElementById('kchart'))
    this.chart.addData(this.generatedKLineDataList())
  }

  /**
   * k
   * @returns {Array}
   */
  generatedKLineDataList (dataSize = 2000, basePrice = Math.random() * 12000) {
    const dataList = []
    let timestamp = new Date().getTime() - 12 * 24 * 3600
    let baseValue = basePrice
    const prices = []
    for (let i = 0; i < dataSize; i++) {
      baseValue = baseValue + Math.random() * 20 - 10
      for (let j = 0; j < 4; j++) {
        prices[j] = (Math.random() - 0.5) * 12 + baseValue
      }
      prices.sort()
      const openIdx = +Math.round(Math.random() * 3).toFixed(0)
      let closeIdx = +Math.round(Math.random() * 2).toFixed(0)
      if (closeIdx === openIdx) {
        closeIdx++
      }
      const volume = Math.random() * 100

      timestamp += 60 * 1000

      const kLineModel = {
        open: prices[openIdx],
        low: prices[0],
        high: prices[3],
        close: prices[closeIdx],
        volume: volume,
        timestamp,
        turnover: prices[closeIdx] * volume
      }
      dataList.push(kLineModel)
    }
    return dataList
  }

  /**
   * 
   */
  exportConfig = () => {
    const file = new File([JSON.stringify(this.chart.getConfig())], 'config.json', { type: 'text/plain;charset=utf-8' })
    FileSaver.saveAs(file)
  }

  /**
   * 
   * @param key
   * @param value
   */
  onConfigChange = (key, value) => {
    if (this.chart) {
      const c = set(this.chart.getConfig(), key, value)
      this.chart.setConfig(c)
    }
  }

  /**
   * 
   * @param specialType
   * @param value
   */
  onSpecialConfigChange = (specialType, value) => {
    switch (specialType) {
      case 'addData': {
        const dataList = this.chart.getDataList()
        const basePrice = (dataList[dataList.length - 1] || {}).close
        const kLineData = this.generatedKLineDataList(1, basePrice)[0]
        this.chart.addData(kLineData)
        break
      }
      case 'picture': {
        const url = this.chart.getConvertPictureUrl()
        const a = document.createElement('a')
        a.download = ''
        a.href = url
        document.body.appendChild(a)
        a.click()
        a.remove()
        break
      }
      case 'defaultRange': {
        this.chart.setDefaultRange(value)
        break
      }
      case 'minRange': {
        this.chart.setMinRange(value)
        break
      }
      case 'maxRange': {
        this.chart.setMaxRange(value)
        break
      }
      case 'volChart': {
        this.chart.showVolChart(value)
        break
      }
      case 'mainIndicator': {
        this.chart.setMainIndicatorType(value)
        break
      }
      case 'subIndicator': {
        this.chart.setSubIndicatorType(value)
        break
      }
      case 'drawLine': {
        this.chart.drawMarker(value.key)
        break
      }
    }
  }

  render () {
    return (
      <div className="kline">
        <div className="kline-setting-container">
          <div className="kline-setting-title-container">
            <div className="kline-setting-title-icon-container">
              <a
                className="icon-github kline-setting-title-icon"
                target="_blank"
                rel="noopener noreferrer nofollow"
                href="https://github.com/liihuu/klineweb"/>
              <span>
                K
                <span className="kline-setting-title-version">&nbsp;(v{klinecharts.version})</span>
              </span>
            </div>
            <button
              className="kline-setting-button"
              onClick={this.exportConfig}>
              
            </button>
          </div>
          <SettingList
            onConfigChange={this.onConfigChange}
            onSpecialConfigChange={this.onSpecialConfigChange}/>
        </div>
        <div id="kchart" className="kline-chart"/>
      </div>
    )
  }
}

export default KLine
