import React, { PureComponent } from 'react'
import './select.less'

class Select extends PureComponent {
  static defaultProps = {
    dataSource: [],
    onSelectChange: () => {}
  }

  constructor (props) {
    super(props)
    this.rootDomClick = this.rootDomClickHandler.bind(this)
    this.rootDomBlur = this.rootDomBlurHandler.bind(this)
    this.state = {
      isShow: false,
      value: props.defaultValue
    }
  }

  rootDomClickHandler (e) {
    e.stopPropagation()
    this.setState({
      isShow: !this.state.isShow
    })
  }

  rootDomBlurHandler (e) {
    e.stopPropagation()
    this.setState({
      isShow: false
    })
  }

  rowClick = data => e => {
    e.stopPropagation()
    this.props.onSelectChange(data.key)
    this.setState({
      value: data,
      isShow: false
    })
  }

  render () {
    return (
      <div
        tabIndex={0}
        className={`select ${this.state.isShow ? 'select-show' : ''}`}
        onBlur={this.rootDomBlur}>
        <div
          className="select-value-container"
          onClick={this.rootDomClick}>
          <span>{this.state.value.text || this.state.value.key}</span>
          <i className="select-value-arrow"/>
        </div>
        <ul className="select-drop-list">
          {
            this.props.dataSource.map(data => {
              return (
                <li
                  key={data.key}
                  className="select-drop-list-row"
                  onClick={this.rowClick(data)}>
                  {data.text || data.key}
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default Select
