import React, { PureComponent } from 'react'
import './expand-list.less'

class ExpandList extends PureComponent {
  static defaultProps = {
    dataSource: [],
    onItemClick: () => {}
  }

  state = {
    isShowList: false
  }

  onRowClick = (data) => () => {
    this.props.onItemClick(data)
    this.setState({
      isShowList: false
    })
  }

  render () {
    const { isShowList } = this.state
    const dataType = isShowList.toString()
    return (
      <div className="expand-list">
        <i
          datatype={dataType}
          className="icon-draw expand-list-icon"
          onClick={() => { this.setState({ isShowList: !isShowList }) }}/>
        <i datatype={dataType} className="expand-list-arrow"/>
        <ul
          datatype={dataType}
          className="expand-list-container">
          {
            this.props.dataSource.map(data => {
              return (
                <li key={data.key} onClick={this.onRowClick(data)}>{data.text}</li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default ExpandList
