import React, { PureComponent } from 'react'
import { Scrollbars } from 'react-custom-scrollbars'
import Switch from '../../../component/switch/Switch'
import InputNumber from '../../../component/inputnumber/InputNumber'
import Select from '../../../component/select/Select'
import ColorPicker from '../../../component/colorpicker/ColorPicker'
import RadioGroup from '../../../component/radiogroup/RadioGroup'
import Button from '../../../component/button/Button'
import ExpandList from '../../../component/expandlist/ExpandList'

import {
  xAxisStyles, yAxisStyles, mainChartStyles, indicatorChartStyles,
  tooltipStyles, markerStyles, otherStyles, commonStyles
} from './data'

class SettingList extends PureComponent {
  static defaultProps = {
    onConfigChange: () => {},
    onSpecialConfigChange: () => {}
  }

  /**
   * 
   * @param style
   * @returns {Function}
   */
  onValueChange = (style) => (value) => {
    if (style.special) {
      this.props.onSpecialConfigChange(style.special, value)
    } else {
      this.props.onConfigChange(style.key, value)
    }
  }

  /**
   * 
   * @param style
   * @returns {null|*}
   */
  getComponent (style) {
    switch (style.componentType) {
      case 'switch': {
        return (
          <Switch
            defaultValue={style.defaultValue}
            onSwitchChange={this.onValueChange(style)}/>
        )
      }
      case 'input': {
        return (
          <InputNumber
            defaultValue={style.defaultValue}
            onNumberChange={this.onValueChange(style)}/>
        )
      }
      case 'select': {
        return (
          <Select
            dataSource={style.dataSource || []}
            defaultValue={style.defaultValue}
            onSelectChange={this.onValueChange(style)}/>
        )
      }
      case 'color': {
        return (
          <ColorPicker
            defaultValue={style.defaultValue}
            onColorChange={this.onValueChange(style)}/>
        )
      }
      case 'radio': {
        return (
          <RadioGroup
            defaultValue={style.defaultValue}
            dataSource={style.dataSource}
            onRadioChange={this.onValueChange(style)}/>
        )
      }
      case 'button': {
        return (
          <Button
            text={style.buttonText}
            onClick={this.onValueChange(style)}/>
        )
      }
      default: {
        return (
          <ExpandList
            dataSource={style.dataSource}
            onItemClick={this.onValueChange(style)}/>
        )
      }
    }
  }

  /**
   * 
   */
  renderStyleSettingList (type) {
    let styls = []
    let title = ''
    switch (type) {
      case 'xAxis': {
        styls = xAxisStyles
        title = ''
        break
      }
      case 'yAxis': {
        styls = yAxisStyles
        title = ''
        break
      }
      case 'mainChart': {
        styls = mainChartStyles
        title = ''
        break
      }
      case 'indicatorChart': {
        styls = indicatorChartStyles
        title = ''
        break
      }
      case 'tooltip': {
        styls = tooltipStyles
        title = ''
        break
      }
      case 'marker': {
        styls = markerStyles
        title = ''
        break
      }
      case 'other': {
        styls = otherStyles
        title = ''
        break
      }
      case 'common': {
        styls = commonStyles
        title = 'API'
        break
      }
    }
    const titleDom = <p key={`${type}-title`} className="kline-setting-list-title">{title}</p>
    const styleDomList =
      <ul
        key={`${type}-list`}
        className="kline-setting-list">
        {
          styls.map((style) => {
            return (
              <li
                key={style.key || style.special}
                className="kline-setting-list-row">
                <span>{style.text}</span>
                {this.getComponent(style)}
              </li>
            )
          })
        }
      </ul>
    return [titleDom, styleDomList]
  }

  render () {
    return (
      <Scrollbars style={{ height: 'calc(100% - 60px)' } }>
        <div className="kline-setting-list-container">
          { this.renderStyleSettingList('common') }
          { this.renderStyleSettingList('marker') }
          { this.renderStyleSettingList('mainChart') }
          { this.renderStyleSettingList('indicatorChart') }
          { this.renderStyleSettingList('xAxis') }
          { this.renderStyleSettingList('yAxis') }
          { this.renderStyleSettingList('tooltip') }
          { this.renderStyleSettingList('other') }
        </div>
      </Scrollbars>
    )
  }
}

export default SettingList
